body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.errorToast {
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 10000001;
    position: fixed; 
    display: -webkit-flex; 
    display: flex;
    -webkit-justify-content:center;
            justify-content:center;
    -webkit-align-items: center;
            align-items: center; 
  }
  
  .errorToast__text {
    max-width: 300px;
    max-height: 300px;
    padding: 15px;
    border-radius: 10px;
    color: #fff;
    font-size: 14px;
    background-color: #000;
  }
.category {
  text-align: center;
  border-top: solid 1px #e1e1e1;
  border-bottom: solid 1px #e1e1e1;
  padding: 0 0 30px;
  color: #333;
  background-color: #fff;
}

.category__section {
  width: 20%;
  padding: 13px 0 0;
  float: left;
  font-size: 13px;
  text-align: center;
}

.category__icon {
  width: 44px;
  height: 44px;
  margin: 0 auto 2px;
}

.category__text {
  display: inline-block;
  vertical-align: top;
  margin: 0;
}

.slick-dots li {
  margin: 0;
}

.slick-dots li.slick-active button:before {
  color: #f5734b;
}

.headline {
  display: -webkit-flex;
  display: flex;
  background-color: #fff;
}

.headline__logo {
  width: 50px;
  height: 65px;
  -webkit-flex-basis: 50px;
          flex-basis: 50px;
  background-image: url('http://www.sclance.com/pngs/movie-clapper-png/movie_clapper_png_902062.png');
  background-position: 0px 10px;
  background-repeat: no-repeat;
  background-size: 50px;
}

.headline__logo::after {
  background-image: -webkit-linear-gradient(left,#e1e1e1,#e1e1e1 50%,transparent 50%);
  background-image: linear-gradient(90deg,#e1e1e1,#e1e1e1 50%,transparent 50%);
  background-position: top right;
  background-size: 1px 100%;
  background-repeat: no-repeat;
  content: "";
  display: block;
  height: 30px;
  margin-top: 20px;
}

.headline__slider {
  height: 65px;
  -webkit-flex: 1 1;
          flex: 1 1; 
  overflow: hidden;
}

.headline__sliderInner {
  display: inline-block;
  height: 65px;
  width: 100%;
}

.headline__sliderTitle {
  width: calc(100% - 75px);
  height: 100%;
  float: left;
  line-height: 20px;
  font-size: 14px;
  font-weight: bold;
  color: #111;
  padding-left: 10px;
  padding-right: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.headline__sliderImgWrapper {
  float: left;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.headline__sliderImg {
  width: 45px;
  height: 45px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

